.o-header {
  background-color: #fff1e0;
  color: #333333; }

.o-header__row {
  border-bottom: 1px solid #cec6b9; }

.o-header__container {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  min-width: 240px;
  max-width: 1220px;
  /*autoprefixer: off*/
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  max-width: 1220px;
  padding-left: 10px;
  padding-right: 10px; }
  @media (min-width: 46.25em) {
    .o-header__container {
      padding-left: 20px;
      padding-right: 20px; } }
  @media (min-width: 46.25em) {
    .o-grid-snappy .o-header__container, .o-header__container--snappy {
      max-width: 740px; } }
  @media (min-width: 61.25em) {
    .o-grid-snappy .o-header__container, .o-header__container--snappy {
      max-width: 980px; } }
  @media (min-width: 76.25em) {
    .o-grid-snappy .o-header__container, .o-header__container--snappy {
      max-width: 1220px; } }
  @media \0screen {
    .o-header__container {
      padding-left: 20px;
      padding-right: 20px;
      width: 980px; } }
  .o-header__container:before, .o-header__container:after {
    content: '';
    display: table; }
  .o-header__container:after {
    clear: both; }

.o-header__visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  width: 1px;
  height: 1px; }

.o-header__top-wrapper {
  display: table;
  width: 100%; }

